import { Button, Checkbox, Col, Input, Row, Select, Switch } from 'antd';
import { useEffect, useState } from 'react';

const COUNTRY = [
    {
        id: 1,
        title: 'United Kingdom',
    },
    {
        id: 2,
        title: 'Germany',
    },
    {
        id: 3,
        title: 'France',
    },
    {
        id: 4,
        title: 'Italy',
    },
    {
        id: 5,
        title: 'Spain',
    },
    {
        id: 6,
        title: 'Netherlands',
    },
    {
        id: 7,
        title: 'Poland',
    },
    {
        id: 8,
        title: 'Sweden',
    },
    {
        id: 9,
        title: 'Belgium',
    },
];

export const App = () => {
    const [MAIN, useMAIN] = useState(1.17);
    const [MORE, useMORE] = useState(0.83);
    const [isEN, setIsEN] = useState(true);
    const [TOTAL_COUNT_ONE, setTotalCountOne] = useState(9);
    const [TOTAL_COUNT_TWO, setTotalCountTwo] = useState(9);
    const [PARENTS, setParent] = useState(1);
    const [ASINS, setAssins] = useState(10);
    const [isStore, setStore] = useState(false);
    const [isEU, setIsEu] = useState(false);
    const [isApluse, setIsApluse] = useState('no');
    const [ownSellerAccount, setOwnSellerAccount] = useState(false);

    const onChangeCountryOne = (e) => {
        if (e.target.name === 'United Kingdom') {
            setIsEN(e.target.checked);
        }
        setTotalCountOne(e.target.checked ? TOTAL_COUNT_ONE + 1 : TOTAL_COUNT_ONE - 1);
    };
    const onChangeCountryTwo = (e) => {
        setTotalCountTwo(e.target.checked ? TOTAL_COUNT_TWO + 1 : TOTAL_COUNT_TWO - 1);
    };

    const handleStore = () => {
        setStore(!isStore);
        isStore ? setTotalCountTwo(0) : setTotalCountTwo(9);
    };

    const handleParent = (e) => {
        setParent(+e.target.value);
    };
    const handleAssins = (e) => {
        setAssins(+e.target.value);
    };

    useEffect(() => {
        phase_one();
    }, [TOTAL_COUNT_ONE, TOTAL_COUNT_TWO, isApluse, isStore]);

    // ====== CALC ======

    const phase_one = () => {
        const element_one = () => {
            const B = () => {
                const totalCount = isEN ? TOTAL_COUNT_ONE - 1 : TOTAL_COUNT_ONE;
                const result = PARENTS * MAIN + PARENTS * MORE * totalCount;
                return {
                    totHrs: result,
                    totEur: 80 * result,
                    totMonth: (80 * result) / 4,
                };
            };

            const A = () => {
                const selectResult = () => {
                    if (isApluse === 'no') {
                        return 0;
                    }

                    if (isApluse === 'brandStory') {
                        return isEN ? 2 + 1 * (TOTAL_COUNT_ONE - 1) : 2 + 1 * TOTAL_COUNT_ONE;
                    }

                    if (isApluse === 'perParent') {
                        return isEN
                            ? PARENTS * 2 + PARENTS * 1 * (TOTAL_COUNT_ONE - 1)
                            : PARENTS * 2 + PARENTS * 1 * TOTAL_COUNT_ONE;
                    }
                };

                return {
                    totHrs: selectResult(),
                    totEur: 80 * selectResult(),
                    totMonth: (80 * selectResult()) / 40,
                };
            };

            const Store = () => {
                const result = isStore ? 20 + 10 * (TOTAL_COUNT_TWO - 1) : 0;

                return {
                    totHrs: result,
                    totEur: 80 * result,
                    totMonth: (80 * result) / 4,
                };
            };

            return {
                tot_eur_content: B().totMonth + A().totMonth + Store().totMonth,
                no_name: (B().totMonth + A().totMonth + Store().totMonth) * 4,
            };
        };
        // {no_name, tot_eur_content}

        const element_two = () => {
            const A = () => {
                return {
                    total: 400 + 300 + 800 + 480 + 1000,
                    month: (400 + 300 + 800 + 480 + 1000) / 4,
                };
            };

            const B = () => {
                const result = 0.5 * PARENTS + 0.25 * TOTAL_COUNT_ONE * PARENTS;

                return {
                    totHrs: result,
                    totEur: result * 60,
                    month: (result * 60) / 4,
                };
            };

            const C = () => {
                const result = 0.1 * PARENTS * TOTAL_COUNT_ONE + 0.25 * TOTAL_COUNT_ONE;

                return {
                    totHrs: result,
                    totEur: result * 60,
                    month: (result * 60) / 4,
                };
            };

            const D = () => {
                return {
                    totHrs: isEU ? 0 : 160,
                    month: isEU ? 0 : 160 / 4,
                };
            };

            const E = () => {
                return {
                    totHrs: ownSellerAccount ? 0 : 240 + 300,
                    month: ownSellerAccount ? 0 : (240 + 300) / 4,
                };
            };

            const F = () => {
                const result =
                    (PARENTS <= 10 ? PARENTS : 10) *
                    0.8 *
                    (TOTAL_COUNT_ONE > 2 ? 3 : TOTAL_COUNT_ONE);

                return {
                    totHrs: result,
                    month: result * 65,
                };
            };

            return {
                F: F().month,
                E: E().month,
                D: D().month,
                C: C().month,
                B: B().month,
                A: A().month,
            };
        };

        return (
            element_one().tot_eur_content +
            element_two().F +
            element_two().E +
            element_two().D +
            element_two().C +
            element_two().B +
            element_two().A
        )
            .toFixed(2)
            .toString()
            .split('.');
    };

    const phase_two = () => {
        const oneOff = 200 + 150;
        const inventory = 100;
        const EU_import = isEU ? 0 : 160 / 4;
        const reportVat = ownSellerAccount ? 0 : (240 + 300) / 4;

        const roundCeil = (sum, num) => {
            return Math.ceil(sum / num) * num;
        };

        const adv = () => {
            return {
                totHrs: (PARENTS < 10 ? PARENTS : 10) * 0.4 * TOTAL_COUNT_ONE,
                month: (PARENTS < 10 ? PARENTS : 10) * 0.4 * TOTAL_COUNT_ONE * 65,
            };
        };

        const modifications = () => {
            return {
                totHts: 0.01 * ASINS * TOTAL_COUNT_ONE,
                month: 120 + 0.01 * ASINS * TOTAL_COUNT_ONE * 60,
            };
        };

        return roundCeil(
            oneOff + inventory + EU_import + reportVat + adv().month + modifications().month,
            5,
        );
    };

    // ====== ./CALC ======

    return (
        <Row justify="center" align="middle" style={{ height: '100vh' }}>
            <Col>
                <Row
                    style={{
                        backgroundColor: '#fff',
                        padding: '20px',
                        borderRadius: '10px',
                        width: '800px',
                        flexDirection: 'column',
                    }}>
                    <Col style={{ fontSize: 25, fontWeight: 'bold' }}>AIaaS V2</Col>
                    <Col style={{ fontWeight: 'lighter' }}>
                        Expanding your Amazon presence to Europe without barriers is an opportunity
                        hard to miss. Let’s calculate how much would you need to invest to
                        experience stable long term growth!
                    </Col>
                    <Col style={{ marginTop: 20 }}>
                        In which of these countries would you like to distribute your goods?
                        <Row style={{ marginTop: 10 }}>
                            {COUNTRY.map((el) => (
                                <Col key={el.id} span={8}>
                                    <Checkbox
                                        name={el.title}
                                        defaultChecked={true}
                                        onChange={onChangeCountryOne}>
                                        {el.title}
                                    </Checkbox>
                                </Col>
                            ))}
                        </Row>
                    </Col>
                    <Col
                        style={{
                            marginTop: 10,
                        }}>
                        <Row style={{ marginTop: 10 }} gutter={30}>
                            <Col span={12}>
                                Number of parent products?
                                <Input
                                    type="number"
                                    placeholder="Number"
                                    defaultValue={PARENTS}
                                    onChange={handleParent}
                                />
                            </Col>
                            <Col span={12}>
                                Number of ASINs?
                                <Input
                                    type="number"
                                    placeholder="Number"
                                    defaultValue={ASINS}
                                    onChange={handleAssins}
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col
                        style={{
                            marginTop: 10,
                        }}>
                        <Row style={{ marginTop: 10 }} gutter={30}>
                            <Col span={12}>
                                A+?
                                <Select
                                    defaultValue={isApluse}
                                    style={{ width: '100%' }}
                                    onChange={(value) => setIsApluse(value)}>
                                    <Select.Option value="no">No</Select.Option>
                                    <Select.Option value="brandStory">Brand Story</Select.Option>
                                    <Select.Option value="perParent">Per parent</Select.Option>
                                </Select>
                            </Col>
                            <Col span={3} style={{ display: 'flex', alignItems: 'center' }}>
                                <Switch
                                    size="small"
                                    // defaultChecked={IS_EUROPE}
                                    onChange={(checked) => handleStore()}
                                    style={{ marginRight: 5 }}
                                />
                                Store
                            </Col>
                            <Col span={3} style={{ display: 'flex', alignItems: 'center' }}>
                                <Switch
                                    size="small"
                                    defaultChecked={isEU}
                                    onChange={(checked) => setIsEu(checked)}
                                    style={{ marginRight: 5 }}
                                />
                                EU
                            </Col>
                            <Col span={6} style={{ display: 'flex', alignItems: 'center' }}>
                                <Switch
                                    size="small"
                                    defaultChecked={ownSellerAccount}
                                    onChange={(checked) => setOwnSellerAccount(checked)}
                                    style={{ marginRight: 5 }}
                                />
                                Own seller account
                            </Col>
                        </Row>
                    </Col>

                    {isStore && (
                        <Col style={{ marginTop: 20 }}>
                            In which of these countries would you like to distribute your goods?
                            <Row style={{ marginTop: 10 }}>
                                {COUNTRY.map((el) => (
                                    <Col key={el.id} span={8}>
                                        <Checkbox
                                            name={el.title}
                                            defaultChecked={true}
                                            onChange={onChangeCountryTwo}>
                                            {el.title}
                                        </Checkbox>
                                    </Col>
                                ))}
                            </Row>
                        </Col>
                    )}

                    <Col style={{ marginTop: 20 }}>
                        <Row style={{ marginTop: 10 }} justify="space-between">
                            <Col span={11}>
                                3. Your email
                                <Input
                                    type={'email'}
                                    placeholder="Email"
                                    style={{ marginTop: 5 }}
                                />
                            </Col>
                            <Col span={11}>
                                4. Your link to online catalogue
                                <Input
                                    placeholder="Amazon page / Webshop"
                                    style={{ marginTop: 5 }}
                                />
                            </Col>
                        </Row>
                    </Col>
                    <Col
                        style={{
                            marginTop: 20,
                            backgroundColor: '#87A794',
                            padding: '10px 30px',
                            color: '#fff',
                        }}>
                        <Row align="middle">
                            <Col span={12} style={{ fontSize: '17px' }}>
                                Your monthly cost will be
                            </Col>
                            <Col span={12} style={{ display: 'flex', alignItems: 'center' }}>
                                <span style={{ fontSize: 17, marginRight: '5px' }}>€</span>
                                <span style={{ fontSize: 40, fontWeight: 'bold' }}>
                                    {phase_one()[0]}
                                </span>
                                <div
                                    style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        marginLeft: 5,
                                    }}>
                                    <span>. {phase_one()[1]}</span>
                                    <span style={{ fontSize: 12, fontWeight: 'lighter' }}>
                                        / per month for in the initial phase (4 months)
                                    </span>
                                </div>
                            </Col>
                        </Row>
                        <Row align="middle">
                            <Col span={12}></Col>
                            <Col span={12} style={{ display: 'flex', alignItems: 'center' }}>
                                <span style={{ fontSize: 17, marginRight: '5px' }}>€</span>
                                <span style={{ fontSize: 40, fontWeight: 'bold' }}>
                                    {phase_two()}
                                </span>
                                <div
                                    style={{
                                        display: 'flex',
                                        flexDirection: 'column',
                                        marginLeft: 5,
                                    }}>
                                    <span>. 00</span>
                                    <span style={{ fontSize: 12, fontWeight: 'lighter' }}>
                                        / per month for the rest of our partnership 10% of sales
                                        excl vat, with minimum retainer{' '}
                                    </span>
                                </div>
                            </Col>
                        </Row>
                    </Col>
                    <Col style={{ padding: '30px 40px' }}>
                        <Row align="middle" gutter={20}>
                            <Col span={12} style={{ fontSize: 12, fontWeight: 'lighter' }}>
                                Interested in a detailed offer? Leave your email and catalog link.
                                We will contact you shortly.
                            </Col>
                            <Col span={12}>
                                <Button
                                    type="primary"
                                    size="large"
                                    style={{
                                        width: '100%',
                                        background: '#4E8B64',
                                        border: '#4E8B64',
                                        borderRadius: '50px',
                                    }}>
                                    Send
                                </Button>
                            </Col>
                        </Row>
                    </Col>
                </Row>
            </Col>
        </Row>
    );
};
